@import "../../assets/scss/app";

.homePage {
  display: flex;
  justify-content: center;

  .links {
    display: flex;
    flex-direction: column;
    border: 3px dashed #249124;
    padding: 5px 50px;
    border-radius: 10px;

    .link {
      font-size: 32px;
      text-decoration: none;
      color: #000;
      transition: 0.3s;

      span {
        display: inline-block;
        font-size: 36px;
        font-weight: bold;
        color: #fcc86c;
        background: #249124;
        border-radius: 3px;
        padding: 2px;
        transition: 0.3s;
      }

      &:not(:first-child) {
        margin-top: 3px;
      }

      &:hover {
        color: grey;

        span {
          background: #fcc86c;
          color: #249124;
        }
      }
    }
  }
}